<template>
    <h1 class="title">二维码生成器</h1>
    <div class="box">
        <img :src="imgsrc" class="qrcode img-thumbnail">
        <br>
        <textarea v-model="input" class="input form-control"></textarea>
        <button @click="gen" type="button" class="btn btn-primary btn1">生成</button>
        <button @click="huan" type="button" class="btn btn-primary btn2">换一个</button>
    </div>
    
</template>

<script setup>
import { ref } from 'vue'; 

let input = ref("")
let imgsrc = ref("")
import QRCode from 'qrcode'
let opts = {
  errorCorrectionLevel: 'H',
  type: 'image/jpeg',
  quality: 10,
    margin: 1,
    maskPattern: 0,
    width: 1000
}

console.log(opts)
QRCode.toDataURL('hello', opts, function (err, url) {
    console.log(url);
    imgsrc.value = url
})
function gen() {
    if (input.value === '') {
        input.value = "请输入文本";
        return;
    }
    QRCode.toDataURL(input.value, opts, function (err, url) {
        imgsrc.value = url;

    })
}
function huan() {
    opts.maskPattern = parseInt(Math.random() * 8) % 8;
    gen();
}
</script>

<style scoped>
.box{
    margin-top: 100px;
    background: rgb(52, 58, 64);
    margin-left: 15%;
    margin-right: 15%;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
}
.qrcode{
    width: 70%;
    max-width: 300px;
}
.input{
    margin-top: 20px;
    margin-left: 50%;
    transform: translate(-50%);
}
.form-control{
    max-width: 400px;
    height: 30px;
    text-align: center;
}
.btn1{
    margin-top: 20px;
    margin-right: 10px;
}
.btn2{
    margin-top: 20px;
    margin-left: 10px;
}
.title{
    position: relative;
    top: 40px;
    font-weight: 600;
    color: azure;
}

</style>